html, body {
  font: {
    family: $primary-font;
    size: 16px; }
  background: {
    color: $bg; } }

a {
  color: $links;

  &:hover {
    color: transparentize($links, 0.2); }

  &:hover, &:active, &:visited, &:link {
    text-decoration: none; }

  &:visited {
    color: $links; } }

hr {
  border: 0px;
  height: 1px;
  background: transparentize($hr-color, 0.3);
  width: 50%;
  margin: 50px auto; }

textarea {
  margin: 15px 0; }

.row {
  margin-left: 0;
  margin-right: 0; }

.read-more {
  color: $featured-text;
  background: transparentize($read-more, 0.2);
  text-transform: uppercase;
  text-decoration: none;
  text-align: center;
  font: {
    family: $primary-font;
    size: 24px;
    weight: 300; }
  border-radius: 0px;
  padding: 10px 30px;
  @include transition(0.2s ease-in-out);

  &:hover {
    background: $read-more;
    color: $featured-text;
    @include transition(0.2s ease-in-out); }

  &:hover, &:active, &:visited, &:link {
    text-decoration: none; }

  &:visited {
    color: $featured-text; } }

.form-margin-top {
  margin: 15px auto 0px; }
