.navbar {
  text-align: center;

  hr {
    width: 800px;
    margin: 0px auto; }

  ul {
    list-style-type: none;
    margin: 0;
    padding: 0;

    li {
      display: inline-block;
      margin: 0 20px;
      text-transform: uppercase;
      padding: 10px 0;
      font: {
        size: 12px;
        family: $primary-font; } } }


  .searchForm {
    color: $post-paragraph;
    .searchBox {
      text-transform: uppercase;
      font: {
        size: 12px;
        family: $primary-font; }
      padding: 0;
      margin: 0;
      border: 0;
      border-bottom: 1px solid $hr-color;
      width: 57px;
      color: $post-paragraph; }

    .btn {
      display: none; } } }
